    p.mt-5 La seguridad tiene como objetivo la prevencin de actos de interferencia ilcita, evitando que viajen en las aeronaves objetos o personas que puedan constituir una amenaza para el vuelo, es por esto que la OACI (Organizacin de Aviacin Civil Internacional) bajo el Anexo 17 del convenio de Chicago establece las normas y mtodos recomendados para la industria y regula el trnsito areo internacional, estableciendo procedimientos tales como:
