    p.mt-5 Estos y otros apartes hacen parte de seguridad de la aviacin civil, siendo las normas que cada operador areo debe cumplir para operar en la aviacin civil de manera segura, teniendo en cuenta que el incumplimiento de estas normas acarrear multas millonarias e incluso puede llegar a perder la licencia de navegacin area en un pas.

    .titulo-segundo.mt-5
      #t_1_1.h4 1.1  Documentacin migratoria
    .row.mt-3
      .col-12.col-lg-8
        p Entendiendo que documentacin es el conjunto de documentos generalmente oficiales, con que se aprueba o acredita un proceso de migracin que se genera cuando hay desplazamiento de un individuo o poblacin de un lugar de origen o residencia a otro con fines tursticos, laborales o residenciales; se puede deducir entonces que la documentacin migratoria es el conjunto de documentos requeridos para poder desplazarse de un punto de origen a un destino final, esta migracin puede ser interna; es decir, dentro de un mismo pas o externa (internacional).
        p.mt-3 La autoridad aeronutica de cada pas es la encargada de regular la documentacin requerida para vuelos nacionales e internacionales; para Colombia es la UAEAC (Unidad Administrativa Especial Aeronutica Civil de Colombia), que  a travs de la circular reglamentaria NID: 4302082.10.12, dan a conocer los documentos vlidos para pasajeros en vuelos nacionales e internacionales que estn contemplados en el reglamento aeronutico de Colombia en la Resolucin 3502 del 28 de junio del 2012, en el captulo V e indica lo siguiente:
      .col-6.col-lg-4.offset-3.offset-lg-0
        figure
