            .h4 Tratados internacionales
            p.mt-3 Un tratado internacional es un trmino que designa los acuerdos jurdicos entre diferentes estados o entre el Estado y un organismo internacional, estableciendo compromisos que faciliten las relaciones bilaterales entre naciones a nivel econmico, poltico, cultural o cientfico y son regidos por las normativas jurdicas del derecho internacional.
            p.mt-3 Estos tratados pueden ser de diferentes tipos, segn la materia pueden ser tratados comerciales, polticos, econmicos, fiscales, sociales y humanitarios; en la actualidad Colombia cuenta con 16 tratados vigentes con diferentes pases, entre ellos Mercosur y Comunidad Andina CAN, los cuales intervienen directamente en la documentacin migratoria.  A continuacin se ve ms detalladamente:
