            .h4 Comunidad Andina CAN 
            p.mt-3 La comunidad andina es hoy un organismo subregional con personera jurdica internacional integrada por Bolivia, Colombia, Ecuador y Per; que le ha otorgado la condicin de pas miembro asociado a Chile y a los estados parte de Mercosur y tiene como propsito mejorar el nivel de vida y desarrollo equilibrado de los pases miembros.
            p.mt-3 En consecuencia, el propsito de este organismo es que los ciudadanos andinos tengan derecho a ingresar a otro pas miembro de la comunidad andina en calidad de turistas sin necesidad de pasaporte o visa, es decir, con el documento de identificacin nacional o cdula; cabe aclarar que cada estado puede solicitar acciones, documentos o normas adicionales, como, por ejemplo: 
