            p.mt-3 El mercado comn del sur o MERCOSUR es un mecanismo de integracin econmico y comercial creado en 1991 y modificado en 1994; est compuesto por Argentina, Paraguay, Uruguay y Brasil, contando como estados asociados a Chile, Colombia, Ecuador, Guyana, Per, Surinam y Bolivia quien en el 2015 solicit su incorporacin como estado miembro y no asociado. 
            p.mt-3 Por lo tanto y al igual que para los ciudadanos miembros de la comunidad andina, los ciudadanos miembros de Mercosur (Argentina, Paraguay, Uruguay y Brasil) cuentan con el derecho de ingresar a otro pas miembro de este tratado o de la comunidad andina con su documento de identidad en calidad de turista o para eventos culturales o deportivos por un periodo inferior a 90 das.     
