        p.mt-3 A partir del 2015 la Comisin Europea y el Ministerio de Relaciones Exteriores firmaron el acuerdo de exencin del visado para algunos pases de Europa, el cual permitir a los colombianos viajar, sin necesidad de solicitar una Visa Schengen para turismo o cursos cortos por un tiempo mximo de 90 das continuos o no dentro de un periodo de 180 das. Estos pases son: Alemania, Austria, Blgica, Bulgaria, Croacia, Chipre, Repblica Checa, Eslovaquia, Eslovenia, Espaa, Estonia, Finlandia, Grecia, Hungra, Italia, Letonia, Lituania, Luxemburgo, Malta, Pases Bajos, Polonia, Portugal, Rumania, Suecia, Dinamarca, Francia, Suiza, Liechtenstein, Noruega, Islandia.
