              p.text-small En caso de viajar en trnsito por Inglaterra se debe tener en cuenta que si el trnsito es considerado #[strong AIRSIDE]; es decir, que ingresan y salen de este pas el mismo da que tiene los documentos en regla para el siguiente destino y que el trnsito se realizar dentro del mismo aeropuerto este tipo de trnsito solo se permite en los aeropuertos Gatwick, Manchester y London Heathrow.
    .row.mt-5
