            p.mt-3 O visa, es una autorizacin condicionada que otorga un pas a un ciudadano extranjero para entrar y permanecer temporalmente en dicho pas. Los pases que actualmente solicitan visa a los colombianos son: Afganistn, Angola, Arabia Saudita, Argelia, Australia, Bangladesh, Cabo Verde, Camboya, China, Congo, Costa de Marfil, Costa Rica, Egipto, Emiratos rabes, Estados Unidos, Etiopa, Guinea, Hait, India, Indonesia, Irn, Iraq, Irlanda, Japn, Kenia, Lbano, Liberia, Libia, Malasia, Marruecos, Nueva Zelanda, Reino Unido, Senegal, Siria, Sudfrica, Tailandia, Tnez, Vietnam, entre otros.
