      .col-10.offset-1
        .bloque-texto-a.color-secundario.p-4.p-md-4.mb-5 
          .row.m-0.align-items-center.justify-content-between
            .col-lg-8
              .bloque-texto-a__texto.p-4
                p Aunque a la mayora de pases se les solicita visa para el ingreso a este pas, Estados Unidos cuenta con el #[strong Waiver Program] o programa de exencin de visas y permite a los nacionales o ciudadanos de los 39 pases miembros, viajar a los estados unidos por turismo o negocios para una estada mnima de 90 das sin necesidad de obtener visa. En su lugar deben solicitar una autorizacin de viaje a travs del sistema electrnico conocido como #[strong Cdigo EST Electronic System for Travel Authorization], por sus siglas en ingls; este documento debe estar vigente al momento del viaje y tener una validez de 6 meses.
            .col-lg-4.mb-4.mb-lg-0
              figure
                img(src="@/assets/template/tema-1-64.svg", alt="Texto que describa la imagen")
    p.mt-5 Los pases autorizados son:
    p.mt-5 Andorra, Alemania, Australia, Austria, Blgica, Brunei, Chile, Corea del Sur, Dinamarca, Eslovenia, Eslovaquia, Espaa, Estonia, Finlandia, Francia, Grecia, Holanda, Hungra, Islandia, Irlanda, Italia, Japn, Letonia, Liechtenstein, Lituania, Luxemburgo, Malta, Noruega, Nueva Zelanda, Principado de Mnaco, Polonia, Portugal, Repblica Checa, San Marino, Singapur, Suecia.
    p.mt-5 Pero estos no son los nicos documentos vlidos para viajar internacionalmente; tambin  se encuentran
