            p.py-4.px-5 Adicional a los documentos migratorios ya antes mencionados, los pases pueden solicitar documentacin adicional para el ingreso a su nacin, entre ellos se encuentran requisitos de salud como lo es la #[strong vacuna de la fiebre amarilla] (esta fiebre se propaga mediante una especie de mosquito comn en ciertas reas de frica y Amrica del sur y por lo tanto es necesario vacunarse antes de viajar a ciertas regiones).
    .row.mt-5
      .col-10.offset-1
