    p.mt-5 Siguiendo con los requisitos internacionales, los documentos de viaje para poder seguir con el proceso de vuelo son: 
    .row.mt-5
      .col-10.offset-1
        LineaTiempoD.color-primario
          .row(numero="1" titulo="Vacuna de la Fiebre Amarilla")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-69.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small Esta vacuna es necesaria para zonas selvticas y es requisito obligatorio estar vacunado contra la fiebre amarilla para todos los viajeros mayores a nueve (9) meses de edad; el pasajero debe portar el certificado internacional de vacunacin vigente y presentarlo al momento de registrarse y abordar a la aeronave.
          .row(numero="2" titulo="Pasabordo o Pase de Abordar")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-70.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small Documento requerido para el ingreso a salas de abordaje de los pasajeros, este es entregado por el agente de servicios aeroportuarios al cliente, tambin puede ser emitido en la pgina web de la compaa de manera virtual o por medio de los dispositivos de autoservicio que se encuentran en los diferentes aeropuertos. 
          .row(numero="3" titulo="Etiqueta de Equipaje")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-71.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small Dentro de los diferentes documentos que el cliente requiere cuando realiza un vuelo, se encuentra la etiqueta automatizada de equipaje, por regulacin OACI, en el manual de seguridad de aviacin civil AVSEC nos indica que todo equipaje al ser transportado en las bodegas de una aeronave debe estar sistematizado y contener como mnimo: El nombre del pasajero, vuelos, aeropuertos involucrados y peso registrado; la misma debe quedar asociada al PNR (Passenger Name Record) o reserva del viajero.
              p.text-small.mt-3 Existen diferentes tipos de etiquetas, entre las cuales podemos encontrar etiquetas de manejo y etiquetas de manipulacin.
          .row(numero="4" titulo="Etiquetas de Manejo")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-72.png", alt="Texto que describa la imagen")
            .col-8 
              p.text-small #[strong Priority:] Esta etiqueta identifica a los equipajes de pasajeros viajando en clase ejecutiva, miembros del programa de lealtad de alto nivel y personal de la alta gerencia de la empresa.
          .row(numero="5" titulo="Identificacin de equipaje")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-73.png", alt="Texto que describa la imagen")
            .col-8 
              p.text-small #[strong Stanby/Wait List:] Etiqueta para identificar los equipajes de los clientes que no tengan silla confirmada en la aeronave a causa de una sobre reserva del vuelo o por ser pasaje de colaborador.
          .row(numero="6" titulo="Etiquetas no cumplimiento")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-74.png", alt="Texto que describa la imagen")
            .col-8 
              p.text-small #[strong Stanby / Condicional:] Etiqueta que identifica los equipajes que no cumplan con la poltica de equipaje permitido de la compaa y son aceptados, pero sujetos a espacio en la aeronave por lo tanto la compaa puede disponer de un tiempo establecido bajo sus polticas para enviarla al aeropuerto de destino sin generar ningn gasto. 
          .row(numero="7" titulo="Etiquetas conexin estrecha")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-75.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small #[strong Conexin Estrecha:] Se ubicar todo el equipaje que tenga un menor tiempo de conexin al designado por la compaa area con el fin de que estos equipajes sean ubicados de forma estratgica para cumplir con la conexin. 
          .row(numero="8" titulo="Etiquetas servicios especiales")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-76.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small #[strong Servicios Especiales:] Etiqueta que identifica el equipaje de personas viajando con algn servicio especial como menor recomendado o silla de ruedas.
          .row(numero="9" titulo="Etiquetas de Manipulacin")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-77.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small #[strong Heavy:] Etiqueta que identifica los equipajes que tengan un peso mayor de 23 Kilos y hasta 45 Kilos.
          .row(numero="10" titulo="Etiquetas de animal vivo")
            .col-3.mx-3
              figure
                img(src="@/assets/template/tema-1-78.png", alt="Texto que describa la imagen")
            .col-8
              p.text-small #[strong Animal Vivo:] Identifica los huacales y contenedores de animales vivos en bodega.

    .titulo-segundo.mt-5
      #t_1_3.h4 1.3  Seguridad aeroportuaria
    figure.mt-4
      img(src="@/assets/template/tema-1-79.png", alt="Texto que describa la imagen")
    .row.mt-5
      .col-12.col-lg-10
        p La seguridad aeroportuaria son los medios, recursos y el personal capacitado para dar cumplimiento a la normativa internacional de seguridad y control de accesos aeroportuarios; segn el Anexo 17 emitido el 22 de Marzo de 1974 acerca de seguridad de la aviacin (AVSEC; Aviation Security) de la OACI, en el captulo 4 (medidas preventivas de seguridad, aparte 4,2 medidas relativas al control de acceso) se incorporan las normas y recomendaciones en materia de seguridad que deben aplicarse en la aviacin para garantizar un medio de transporte confiable para los pasajeros, incluyendo la proteccin de la aviacin civil en contra de actos de interferencia ilcita.
      .col-2.d-none.d-lg-block
        figure
          img(src="@/assets/template/tema-1-80.svg", alt="Texto que describa la imagen")    
    .h4.mt-5 Actos de Interferencia Ilcita
    p.mt-5 Actos o tentativas destinados a comprometer la seguridad de la aviacin civil y del transporte areo, tales como:
    .row.mt-5
      .col-12.col-lg-7
        ul.lista-ul--color
          li 
            i.fas.fa-brain
            | Apoderamiento Ilcito de aeronaves en vuelo.
          li 
            i.fas.fa-brain
            | Apoderamiento Ilcito de aeronaves en tierra.
          li 
            i.fas.fa-brain
            | Toma de rehenes a bordo de aeronaves o aerdromos.
          li 
            i.fas.fa-brain
            | Intrusin por la fuerza a bordo de aeronaves o aerdromos.
          li 
            i.fas.fa-brain
            | Introduccin a bordo de una aeronave o en un aeropuerto de armas o de artefactos (o sustancias) peligrosos con fines criminales.
          li 
            i.fas.fa-brain
            | Comunicacin de informacin falsa que compromete la seguridad de una aeronave en vuelo o en tierra o la seguridad de los pasajeros, tripulacin, personal de tierra y pblico en un aeropuerto o en el recinto de una instalacin de aviacin civil.
      .col-6.col-lg-5.offset-3.offset-lg-0
        figure
          img(src="@/assets/template/tema-1-81.png", alt="Texto que describa la imagen")
    .row.mt-5
      .col-10.offset-1
        .bloque-texto-a.color-acento-contenido.p-4.p-md-4.mb-5 
          .row.m-0.align-items-center.justify-content-between
            .col-lg-9
              .bloque-texto-a__texto.p-4
                p Para garantizar dicha seguridad los aeropuertos cuentan con personal calificado y capacitado para realizar inspecciones al equipaje con artculos prohibidos, pasajeros y tripulacin de aeronaves, quienes realizan recorridos en las diferentes reas aeroportuarias, adems de puntos de inspeccin mecnicos (Rayos X) para los equipajes facturados y equipaje de mano. Es importante resaltar que todo el material relacionado con la seguridad de la aviacin es considerado informacin sensitiva de seguridad.
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src="@/assets/template/tema-1-82.svg", alt="Texto que describa la imagen")
    .h4.mt-5 Informacin SSI: Informacin sensible de seguridad (Sensitive Security Information)  
    p.mt-4 La informacin SSI o Sensitive Security Information, debe ser protegida contra accesos no autorizados y la empresa area para la cual labore debe disear un plan de sanciones en contra de aquellas personas que compartan o divulguen informacin que lleven o faciliten un acto de interferencia ilcita.
    figure.mt-4
      img(src="@/assets/template/tema-1-83.png" , alt="Texto que describa la imagen")
    .titulo-segundo.mt-5
      #t_1_4.h4 1.4  Mercancas peligrosas en counter
    .row.mt-5
      .col-12
        .bloque-texto-a.color-secundario.p-4.p-md-4.mb-5 
          .row.m-0.align-items-center.justify-content-between
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src="@/assets/template/tema-1-84.svg", alt="Texto que describa la imagen")
            .col-lg-9
              .bloque-texto-a__texto.p-4
                p La reglamentacin de mercancas peligrosas de la IATA, est basada en las instrucciones tcnicas de la organizacin de aviacin civil internacional OACI, dicho manual incorpora requerimientos operacionales adicionales que proveen a los operadores un sistema armonizado para la aceptacin y el transporte de mercancas peligrosas en forma segura y eficiente.
                p.mt-3 La reglamentacin incluye una lista detallada de artculos y sustancias individuales especificando la clasificacin de las naciones unidas de cada artculo o sustancia y su aceptacin para el transporte areo, as como las condiciones para su transporte.
    .h4.mt-5 Base de la Reglamentacin de Mercancas Peligrosas
    p.mt-5 La informacin SSI o Sensitive Security Information, debe ser protegida contra accesos no autorizados y la empresa area para la cual labore debe disear un plan de sanciones en contra de aquellas personas que compartan o divulguen informacin que lleven o faciliten un acto de interferencia ilcita.
    .row.mt-5
      .col-12.col-lg-7
        ol.lista-ol--cuadro
          li 
            .lista-ol--cuadro__vineta
              span 1
            .col-12
              p Comit de expertos de las Naciones Unidas (COE), desarrolla los procedimientos recomendados para el transporte de todo tipo de mercancas peligrosas, excepto el material radioactivo.
          li 
            .lista-ol--cuadro__vineta
              span 2
            .col-12
              p La Agencia Internacional de Energa Atmica (IAEA), desarrolla los procedimientos recomendados para el transporte seguro de materiales radioactivos.
          li 
            .lista-ol--cuadro__vineta
              span 3
            .col-12
              p La Organizacin de Aviacin Civil Internacional (OACI), ha utilizado estas recomendaciones como base para preparar la reglamentacin para el transporte sin riesgo de mercancas peligrosas por va area. La reglamentacin de OACI est codificada en el anexo 18 al convenio internacional de aviacin civil y sus instrucciones tcnicas para el transporte sin riesgo de mercancas peligrosas por va area (documento 9284-AN/905 con sus enmiendas). 
          li 
            .lista-ol--cuadro__vineta
              span 4
            .col-12
              p La reglamentacin de IATA sobre mercancas peligrosas contiene todos los requisitos de las instrucciones tcnicas. Adems, IATA, ha incluido requisitos adicionales que son ms restrictivos que las instrucciones tcnicas y refleja las prcticas normales de la industria o consideraciones operacionales.
      .col-6.col-lg-5.offset-3.offset-lg-0.px-5
        figure
          img(src="@/assets/template/tema-1-85.png", alt="Texto que describa la imagen")
    .row.mt-5 
      .col-8.offset-2
        figure
          img(src="@/assets/template/tema-1-86.png", alt="Texto que describa la imagen")
    .h4.mt-5 Clasificacin
    p.mt-5 Las mercancas peligrosas se clasifican en:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-amarillo")
      .row(titulo="Clase 1: explosivos")
        .col-3.d-none.d-lg-block
          figure
            img(src="@/assets/template/tema-1-87.png", alt="Texto que describa la imagen")
        .col-12.col-lg-9
          ul.lista-ul
            li 
              i.fas.fa-angle-right
              | Artculos y sustancias que presentan un riesgo de explosin masiva. 	
            li.mt-3 
              i.fas.fa-angle-right
              | Artculos y sustancias que presentan riesgo de proyeccin, pero no-explosin 	masiva. 
            li.mt-3 
              i.fas.fa-angle-right
              | Artculos y sustancias que presentan riesgo de incendio, riesgo de que se produzcan pequeos efectos de onda explosiva y/o un pequeo riesgo de explosin masiva. 
            li.mt-3 
              i.fas.fa-angle-right
              | Artculos y sustancias que no presentan ningn riesgo considerable. 	
            li.mt-3 
              i.fas.fa-angle-right
              | Sustancias muy poco sensibles que presentan riesgo de explosin masiva. 
            li.mt-3 
              i.fas.fa-angle-right
              | Sustancia extremadamente insensible que no presenta riesgo de	explosin masiva. 
      div(titulo="Clase 2: gas").row
        .col-3.d-none.d-lg-block
          figure
            img(src="@/assets/template/tema-1-88.png", alt="Texto que describa la imagen")
        .col-12.col-lg-9
          ul.lista-ul
            li 
              i.fas.fa-angle-right
              | Gas inflamable.  
            li.mt-3 
              i.fas.fa-angle-right
              | Gas no inflamable, no txico. 
            li.mt-3 
